<template>
  <div id="app">
    <router-view></router-view>
      <footer class="footer">
        <ul>
          <li><router-link :to="{name:'type',params:{uid:'zsq'}}">张世奇系列</router-link></li>
          <li><router-link :to="{name:'type',params:{uid:'lqh'}}">李奇鸿系列</router-link></li>
          <li><router-link :to="{name:'type',params:{uid:'lrh'}}">罗荣辉系列</router-link></li>
          <li><router-link :to="{name:'type',params:{uid:'scp'}}">石茶盘系列</router-link></li>
        </ul>
      </footer>
  </div>
</template>

<script>
export default {
  name: 'app'
}
</script>

<style scoped>

.footer{
  width: 100%;
  height: 1.3rem;
  position: fixed;
  bottom: 0;
  border-top:1px solid #ddd;
  box-shadow: 0px -2px 7px rgba(0, 0, 0, 0.5);
  background-color: #fff;
  z-index: 1000;
}

.footer ul{
  width: 100%;
  height: 100%;
  font-size: 0;
}
.footer ul li{
  display: inline-block;
  width: 25%;
  height: 100%;
  line-height: 1.3rem;
  font-size: 0.453333rem;
  text-align: center;
  box-sizing: border-box;
  border-right: 1px solid #ddd;
}
.footer ul li a{
  width: 100%;
  height: 100%;
  display: block;
  color: #000;
  text-decoration: none;
}
.footer ul li:active{
  background-color: rgb(0,132,57);
  color: #fff;
}
.footer ul li:last-child{
  border-right: none;
}
</style>



